<template>
  <button
    v-if="(store.step !== 0) & (store.step !== 36)"
    @click.prevent="goBack()"
    class="btn btn-outline-dark px-2"
  >
    <font-awesome-icon icon="fa-arrow-left" class="px-1" /> Zurück
  </button>
</template>

<script setup>
import { dhRegistrationStore } from "@/stores/dh-registration";
const store = dhRegistrationStore();

function goBack() {
  if (store.step == 7 && store.eligibility.anticoagulantBool == false) {
    store.step -= 2;
  } else {
    store.step--;
  }
}
</script>

