<script setup>
import { dhRegistrationStore } from "@/stores/dh-registration";
import { ref, computed, onMounted, onUnmounted } from "vue";
import { zipAT } from "@/assets/zipCodes.js";
const store = dhRegistrationStore();
const emailError = ref(0);
const phoneError = ref(0);
const zipError = ref(0);

const isFormInvalid = computed(() => {
  return (
    store.subject.phone === "" ||
    store.subject.email === "" ||
    store.subject.address.street === "" ||
    store.subject.address.streetNr === "" ||
    store.subject.address.ZIP === "" ||
    store.subject.address.city === ""
  );
});

function validateEmail() {
  const emailRegex =
    /^([a-z0-9_.-]+(?:\.[a-z0-9_.-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9]))?$/gim;
  if (!emailRegex.test(store.subject.email)) {
    emailError.value = 1;
  } else {
    emailError.value = 2;
  }
}

function validatePhone() {
  const phoneRegex =
    /^((?:(?:\+|00)[1-9]{2,3}|0)(?:[\s\-/()]{0,2}[0-9]{1})(?:[()\s\-/}]{0,2}[0-9]){6,13}[0-9])$/gim;
  if (!phoneRegex.test(store.subject.phone)) {
    phoneError.value = 1;
  } else {
    phoneError.value = 2;
  }
}

function validateZIP() {
  if (
    !zipAT.includes(store.subject.address.ZIP) ||
    store.subject.address.ZIP !== parseInt(store.subject.address.ZIP, 10)
  ) {
    zipError.value = 1;
  } else {
    zipError.value = 2;
  }
}

function validateConsentData() {
  validateEmail();
  validatePhone();
  validateZIP();

  if (
    emailError.value === 2 &&
    phoneError.value === 2 &&
    zipError.value === 2
  ) {
    store.step++;
  }
}

function generateMockUp() {
  store.subject.phone = "06641234567";
  store.subject.address.street = "Musterstraße";
  store.subject.address.streetNr = "24";
  store.subject.address.blockNr = "1";
  store.subject.address.doorNr = "12";
  store.subject.address.ZIP = 1010;
  store.subject.address.city = "Wien";
  store.subject.address.country = "Österreich";
}

function emptyForm() {
  store.subject.email = "";
  store.subject.phone = "";
  Object.keys(store.subject.address).forEach(
    (i) => (store.subject.address[i] = "")
  );
}

