function getComponentsByType(components, type) {
  let tempComponent = components.filter((component) =>
    component.types.includes(type)
  );
  try {
    return tempComponent[0].long_name;
  } catch {
    if (type === "postal_code") {
      return null;
    } else {
      return "";
    }
  }
}

function parseAddress(address) {
  const parts = address.split(/[\/]+/);
  console.log(parts);
  let streetName = "";
  let streetNr = "";
  let subpremiseNr = "";
  let unitNr = "";
  if (parts.length === 3) {
    streetName = parts[0];
    streetNr = parts[1];
    unitNr = parts[2];
  } else if (parts.length === 4) {
    streetName = parts[0];
    streetNr = parts[1];
    subpremiseNr = parts[2];
    unitNr = parts[3];
  }
  return {
    streetName: streetName,
    streetNr: streetNr,
    subpremiseNr: subpremiseNr,
    unitNr: unitNr,
  };
}
