<template>
  <h2 class="mt-5">Lassen Sie uns starten!</h2>
  <div class="row my-5">
    <div class="col-6">
      In Teil 1 wird geprüft. ob Sie für die Studie in Frage kommen.
    </div>
    <div class="col-6 text-center">
      Dauer: 3 Minuten<br />
      <font-awesome-icon icon="fa-clock" class="mt-2" style="font-size: 2em" />
    </div>
  </div>

  <form class="needs-validation mt-5" @submit.prevent="validateAge" novalidate>
    <h5 class="text-start">Geben Sie Ihr Alter an:</h5>
    <div class="form-floating mt-4 mb-4">
      <input
        type="number"
        class="form-control"
        :class="[
          ageError === 1 ? 'is-invalid' : '',
          ageError === 2 ? 'is-valid' : '',
        ]"
        id="floatingInput"
        placeholder="75"
        v-model="store.subject.age"
      />
      <label for="floatingInput">Alter (in ganzen Jahren)</label>
      <span style="font-size: 12px"
        >[dev only: use 68 for age-socialsecurity check later]</span
      >
      <div class="invalid-feedback">
        Bitte geben Sie Ihr korrekter Alter als Zahl ein (z.B. 76).
      </div>
    </div>
    <button
      type="submit"
      :disabled="isFormInvalid"
      class="btn btn-proceed btn-lg w-100 mt-5 mb-5"
    >
      Weiter
    </button>
  </form>
</template>

<script setup>
import { ref, computed } from "vue";
import { dhRegistrationStore } from "@/stores/dh-registration";
const store = dhRegistrationStore();
const ageError = ref(0);

const isFormInvalid = computed(() => {
  return !store.subject.age;
});

function validateAge() {
  if (
    store.subject.age < 0 ||
    store.subject.age > 150 ||
    store.subject.age !== parseInt(store.subject.age, 10)
  ) {
    ageError.value = 1;
  } else {
    store.step++;
  }
}
</script>

<style scoped>
.btn-check + .btn {
  color: #142b58;
  border-color: #142b58;
  height: 50px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-check:unchecked + .btn {
  color: #142b58;
}
.btn-check:checked + .btn {
  color: white;
  background-color: #142b58;
}
</style>
